.App {
  text-align: center;
}
body {
  font-family: 'nanumsquare' ;
}
.header-top {
  background: #94e2be;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
  font-weight: 600;
  font-size: 20px;
}

/* 전체 컨테이너 */
.header-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #94e2be;
  padding: 10px 20px;
  font-family: Arial, sans-serif;
  color: black;
}

/* 홈 */
.home {
  font-size: 24px;
  font-weight: bold;
}

/* 메뉴 아이템 */
.header-menu a {
  color: black;
  padding: 10px 15px;
  text-decoration: none;
  font-size: 18px;
}

.header-menu a:hover {
  background-color: #575757;
  border-radius: 5px;
}

/* 검색 및 위치 선택 */
.shortcut-box {
  display: flex;
  align-items: center;
}

.search-shortcut, .area {
  background-color: #444;
  padding: 10px;
  margin-right: 10px;
  border-radius: 5px;
}

/* 로그인 및 회원가입 버튼 */
.join-box {
  display: flex;
  align-items: center;
}

.login-btn, .singup-btn {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  margin-left: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.login-btn:hover, .singup-btn:hover {
  background-color: #45a049;
}

/* 아이콘 스타일 */
.header-menu i {
  margin-right: 8px;
}

.home-img-box{
  width: 100%;
  height: 400px;
  background-color: #94e2be;
  position: relative;

}
.home-img{
  width: 400px;
  height: 340px;
  display: inline-block;
  margin: 0 5px;
  right: 10px;
  position: absolute;
  bottom: 0;
  /* right: 0px;
  bottom: 0px; */
  object-fit: cover;
  background-image: url('./imges/crt.png');
  
}


.section-box{
  display: flex;
  justify-content: center;
    align-items: center;
    flex-direction: column;
}





.content-list-box {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2열 */
  grid-template-rows: repeat(3, auto);   /* 3행 */
  gap: 20px; /* 각 항목 사이의 간격 조정 */
}



.content-list {
  border: 1px solid #ccc; /* 각 이벤트 리스트에 테두리 추가 */
  padding: 10px; /* 내부 여백 추가 */
  border-radius: 5px; /* 둥근 모서리 */
  background-color: #90caaf; /* 배경색 추가 */
}

.content-list{
  text-align: left;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  width: 500px;
  height: 130px;
}

.content-img-box{
  width: 130px;
  height: 130px;
  border: #282c34 ;
  position: relative;

}

.content-img{
  width: 130px;
  height: 130px;

  /* display: inline-block;
  margin: 0 5px;
  right: 10px;
  position: absolute;
  bottom: 0; */
  /* right: 0px;
  bottom: 0px; */
  background-image: url('./imges/test.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}

.content-con{
margin-left: 10px;
}

.event-btn {
  background-color: #503daf; /* 버튼 배경색 */
  color: white; /* 텍스트 색상 */
  border: none; /* 테두리 제거 */
  padding: 15px 32px; /* 내부 여백 */
  text-align: center; /* 텍스트 가운데 정렬 */
  text-decoration: none; /* 텍스트 장식 제거 */
  display: inline-block; /* 인라인 블록으로 표시 */
  font-size: 16px; /* 글꼴 크기 */
  margin: 10px 2px; /* 외부 여백 */
  cursor: pointer; /* 마우스 커서 포인터로 변경 */
  border-radius: 12px; /* 둥근 모서리 */
  transition: background-color 0.3s, transform 0.3s; /* 배경색 및 변형에 대한 부드러운 전환 */
}

.event-btn:hover {
  background-color: #7378bb; /* 호버 시 배경색 */
  transform: scale(1.05); /* 호버 시 확대 효과 */
}






.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
